import React, { useState, useEffect } from "react";
import { Container, Col, Row } from "reactstrap";
import Loader from "react-loader-spinner";
import myInitObject from "../../api";
import DatatablePage from "views/DatatablePage";
import DatePicker from "react-datepicker";
const moment = require("moment");
const Swap = () => {
  const [dailySwap, setdailySwap] = useState([]);
  const [loading, setloading] = useState([]);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');

  useEffect(() => {
    setInterval(() => {
      if (myInitObject.responsDS === undefined) {
        console.log("no data");
      } else {
        setloading(myInitObject.fetchdailyswaps === 0 ? true : false);
        setdailySwap(myInitObject.fetchdailyswaps.rows);
      }
    }, 1000);

  }, []);

  let sd = moment(startDate).format("DD-MM-yyyy");
  let ed = moment(endDate).format("DD-MM-yyyy");
  let result = dailySwap.filter((d) => {
    var time = moment(d.date).format("DD-MM-yyyy");
    if ((sd === 'Invalid date' && ed === 'Invalid date')) {
      return dailySwap
    }
    else if ((sd !== 'Invalid date' && ed === 'Invalid date')) {
      return (time >= sd)
    }
    else if ((sd === 'Invalid date' && ed !== 'Invalid date')) {
      return (time <= ed)
    }
    if (time >= sd && time <= ed) {
      return d;
    }
    //  return (sd <= time && time <= ed);
  });

  console.log(sd, ed, result, "/////////");
  return (
    <Container fluid>
      <div className="pad-y">
        <div className="symbol-box" style={{ marginBottom: "20px" }}>
          <div className="openpositions">
            <span className="p-text">Daily Swaps</span>
          </div>
        </div>
        {loading ? (
          <Loader
            type="ThreeDots"
            color="black"
            height={100}
            width={100}
            style={{ textAlign: "center" }}
          />
        ) : (
            <div className="symbol-box">
              <Row>
                <Col md={12} className="filter-date">
                  <DatePicker
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                    selectsStart
                    placeholderText="mm-dd-yyyy"
                    dateFormat="MM-dd-yyyy"
                    startDate={startDate}
                    endDate={endDate}
                  />
                  <DatePicker
                    selected={endDate}
                    onChange={(date) => setEndDate(date)}
                    selectsEnd
                    placeholderText="mm-dd-yyyy"
                    dateFormat="MM-dd-yyyy"
                    startDate={startDate}
                    endDate={endDate}
                    minDate={startDate}
                  />
                </Col>
              </Row>
              <DatatablePage data={result} />
              {/* <select onChange={(e) => setSortType(e.target.value)}>
              <option value="default">Select Order</option>
              <option value="date">Date</option>
              <option value="account_login">Login</option>
              <option value="symbol">Symbol</option>
              <option value="swap_long">Swap Long</option>
              <option value="swap_short">Swap Short</option>
            </select>
            <table className="table table-responsive table-bordered border-bottom-0 align-th">
              <thead>
                <tr className="text-center thUnset">
                  <th>Date</th>
                  <th>Login</th>
                  <th>Symbol</th>
                  <th>Long</th>
                  <th>Short</th>
                </tr>
              </thead>
              <tbody>
                {dailySwap.length === 0 ? (
                  <tr className="text-center">
                    <td colSpan="5">No Data</td>
                  </tr>
                ) : (
                  dailySwap.map((item) => (
                    <tr key={item.id} className="text-center">
                      <td>
                        {moment(item.date).utc().format("DD-MM-YYYY HH:mm:ss")}
                      </td>
                      <td>{item.account_login}</td>
                      <td>{item.symbol}</td>
                      <td
                        style={{
                          color: `${item.swap_long > 0 ? "green" : "red"}`,
                        }}
                      >
                        <NumberFormat
                          value={item.swap_long}
                          displayType={"text"}
                          thousandSeparator={true}
                          decimalScale={2}
                        />
                      </td>
                      <td
                        style={{
                          color: `${item.swap_short > 0 ? "green" : "red"}`,
                        }}
                      >
                        <NumberFormat
                          value={item.swap_short}
                          displayType={"text"}
                          thousandSeparator={true}
                          decimalScale={2}
                        />
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table> */}
            </div>
          )}
      </div>
    </Container>
  );
};

export default Swap;
